export default "<cn>\r\n#### 可伸缩列\r\n集成 [vue-draggable-resizable](https://github.com/mauricius/vue-draggable-resizable) 来实现可伸缩列。\r\n</cn>\r\n\r\n<us>\r\n#### Resizable column\r\nImplement resizable column by integrate with [vue-draggable-resizable](https://github.com/mauricius/vue-draggable-resizable).\r\n</us>\r\n\r\n<template>\r\n  <a-table bordered :columns=\"columns\" :components=\"components\" :data-source=\"data\">\r\n    <template v-slot:action>\r\n      <a href=\"javascript:;\">Delete</a>\r\n    </template>\r\n  </a-table>\r\n</template>\r\n\r\n<script>\r\nimport Vue from 'vue';\r\nimport VueDraggableResizable from 'vue-draggable-resizable';\r\n\r\nVue.component('vue-draggable-resizable', VueDraggableResizable);\r\nconst columns = [\r\n  {\r\n    title: 'Date',\r\n    dataIndex: 'date',\r\n    width: 200,\r\n  },\r\n  {\r\n    title: 'Amount',\r\n    dataIndex: 'amount',\r\n    width: 100,\r\n  },\r\n  {\r\n    title: 'Type',\r\n    dataIndex: 'type',\r\n    width: 100,\r\n  },\r\n  {\r\n    title: 'Note',\r\n    dataIndex: 'note',\r\n    width: 100,\r\n  },\r\n  {\r\n    title: 'Action',\r\n    key: 'action',\r\n    scopedSlots: { customRender: 'action' },\r\n  },\r\n];\r\nconst data = [\r\n  {\r\n    key: 0,\r\n    date: '2018-02-11',\r\n    amount: 120,\r\n    type: 'income',\r\n    note: 'transfer',\r\n  },\r\n  {\r\n    key: 1,\r\n    date: '2018-03-11',\r\n    amount: 243,\r\n    type: 'income',\r\n    note: 'transfer',\r\n  },\r\n  {\r\n    key: 2,\r\n    date: '2018-04-11',\r\n    amount: 98,\r\n    type: 'income',\r\n    note: 'transfer',\r\n  },\r\n];\r\nconst draggingMap = {};\r\ncolumns.forEach(col => {\r\n  draggingMap[col.key] = col.width;\r\n});\r\nconst draggingState = Vue.observable(draggingMap);\r\nconst ResizeableTitle = (h, props, children) => {\r\n  let thDom = null;\r\n  const { key, ...restProps } = props;\r\n  const col = columns.find(col => {\r\n    const k = col.dataIndex || col.key;\r\n    return k === key;\r\n  });\r\n  if (!col.width) {\r\n    return <th {...restProps}>{children}</th>;\r\n  }\r\n  const onDrag = x => {\r\n    draggingState[key] = 0;\r\n    col.width = Math.max(x, 1);\r\n  };\r\n\r\n  const onDragstop = () => {\r\n    draggingState[key] = thDom.getBoundingClientRect().width;\r\n  };\r\n  return (\r\n    <th {...restProps} v-ant-ref={r => (thDom = r)} width={col.width} class=\"resize-table-th\">\r\n      {children}\r\n      <vue-draggable-resizable\r\n        key={col.key}\r\n        class=\"table-draggable-handle\"\r\n        w={10}\r\n        x={draggingState[key] || col.width}\r\n        z={1}\r\n        axis=\"x\"\r\n        draggable={true}\r\n        resizable={false}\r\n        onDragging={onDrag}\r\n        onDragstop={onDragstop}\r\n      ></vue-draggable-resizable>\r\n    </th>\r\n  );\r\n};\r\nexport default {\r\n  name: 'App',\r\n  data() {\r\n    this.components = {\r\n      header: {\r\n        cell: ResizeableTitle,\r\n      },\r\n    };\r\n    return {\r\n      data,\r\n      columns,\r\n    };\r\n  },\r\n};\r\n</script>\r\n<style lang=\"less\">\r\n.resize-table-th {\r\n  position: relative;\r\n  .table-draggable-handle {\r\n    height: 100% !important;\r\n    bottom: 0;\r\n    left: auto !important;\r\n    right: -5px;\r\n    cursor: col-resize;\r\n    touch-action: none;\r\n    position: absolute;\r\n  }\r\n}\r\n</style>\r\n";